import React from 'react';
import { Modal, Icon, Header, Image } from 'semantic-ui-react';
// Internal
import { modalContentStyles as styles } from '../styles';
import TechList from './TechList';
import Images from './Images';
import Buttons from './Buttons';

const ModalContent = ({ language, item, type }) => {
  return (
    <Modal
      closeIcon
      dimmer='blurring'
      trigger={
        <div>
          {type === 'main' ? (
            <Image
              src={item.mainImage}
              size='small'
              circular
              style={styles.mainImage}
            />
          ) : (
            <Icon
              name={item.icon}
              size='big'
              circular
              bordered
              style={styles.icon}
            />
          )}

          <Header
            style={type === 'main' ? styles.headerMain : styles.header}
            as='h4'
          >
            {language === 'en' ? item.title.eng : item.title.esp}
          </Header>
        </div>
      }
    >
      <Modal.Header style={styles.modalHeader}>
        {language === 'en' ? item.title.eng : item.title.esp}
      </Modal.Header>
      <Modal.Content>
        <Icon size='small' src={item.image} style={{ margin: 'auto' }} />
        <Modal.Description style={styles.mainContent}>
          {language === 'en' ? item.desc.eng : item.desc.esp}
        </Modal.Description>
        {item.image.length > 0 ? (
          <Modal.Description>
            <Images language={language} images={item.image} />
          </Modal.Description>
        ) : null}

        <TechList item={item} />
        <Modal.Actions>
          <Buttons language={language} item={item} />
        </Modal.Actions>
      </Modal.Content>
    </Modal>
  );
};

export default ModalContent;
